<template>
  <div class="engineeringDetail">
    <navigation-bar  @onLeftClick="onClickLeft" :pageName="title" :bothWidth="headerItemWidth" ></navigation-bar>
    <div class="engineeringDetailContent">
      <!--工程进展头部-->
      <div class="main-header">
        <div class="detail-title fontSize13">{{comentsData.TITLE}}</div>
        <div class="detail-time fontSize13">{{comentsData.CREATED_TIME}}</div>
      </div>
      <div class="main-content" v-html="comentsData.CONTENT"></div>
    </div>
  </div>
</template>

<script>
import { getEngineerDetailsService } from "../../../services/propertyService";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "engineeringDetail",
  components:{
      NavigationBar
  },
  data() {
    return {
      comentsData: {},
      projectCode:"",
      headerItemWidth:{
          width:'84px'
      },
      title:'工程进展详情',
    };
  },
  mounted() {
    this.getEngineerDetails();
  },
  methods: {
    getEngineerDetails() {
      let reqData = {
        PROJECT_CODE:this.$route.params.projectCode /*业主所在小区*/
      };
      getEngineerDetailsService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          if (result == "") {
          } else {
            this.comentsData = result;
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    ///随机生成颜色
    getRandomColor() {
      return (
        "#" +
        (function(color) {
          return (color += "0123456789abcdef"[
            Math.floor(Math.random() * 16)
          ]) && color.length == 6
            ? color
            : arguments.callee(color);
        })("")
      );
    },
    onClickLeft() {
      this.$router.back();
    }
  }
};
</script>

<style scoped>
.engineeringDetail {
  height: 100%;
  background-color: #f2f2f2;
  display: flex;
  flex-direction: column;
}
.engineeringDetail .rightBtn{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: -0.05px;
    line-height: 32px;
    padding-right: 10px;
    width: 100%;
    text-align: right;
}
.engineeringDetailContent{
  
  flex: 1;
  overflow-y: scroll;
}
.engineeringDetail .main-header {
  background-color: #fff;
  border-bottom: 1px solid #f8f8f8;
  padding: 10px;
}
.engineeringDetail .detail-title {
  font-weight: bold;
  line-height: 25px;
}
.engineeringDetail .detail-time {
  color: #a19e97;
}
.engineeringDetail .main-content {
  min-height: 200px;
  background-color: #fff;
  padding: 10px;
  font-size: 14px;
}
.engineeringDetail .main-content /deep/ img,p,span{
  width: 100% !important;
}
.engineeringDetail .fontSize13 {
  font-size: 13px;
}
</style>
