import http from "../http";


export { getAppPropertyListService,getAppLifeListService,getEngineerService,getCommentsService,getEngineerDetailsService,addCommentService,getXihuAddrService,deleteCommentService,getIfShowJieshenService }

/* 服务菜单——查询物业应用 */
const getAppPropertyListService = (params) => {
    return new Promise((resolve, reject) => {
        http.post('hisense-systemServer/app/owner/homePage/getAppList.do', params, true, "homeUrl").then(result => {
            resolve(result);
        }).catch(error => {
            reject(error);
        })

    });
}

